	module.exports = {"data":{"markdownRemark":{"html":"<p>社会科学アカデミーには、台湾中央研究所のZ教授と米国スタンフォード大学のW教授が上海に来て、「南中国農村社会生活調査」のアンケートについて話し合った。ここでは彼らは本土の状況にあまり精通しておらず、いくつかの問題は調査なしに設定されています。しかし、これはまた非常に興味深い文化的な違いであり、中国の文化発展の違いも反映しています。</p>\n<p>「あなたの自宅で自分のビジネスを持っていますか？」という質問には、実際には答えられません。中国の現在の経済発展の中で、あなたの家族のビジネスには複数の意味があります。それは、他の企業とのビジネスに投資する業界かもしれませんし、営業を請け負う集合的な業界かもしれません。これらの異なる形では、農民の生活にさまざまな影響があり、経済的にも異なる意味があります。また、この人類学的研究には異なる意味があります。私たちがそれを説明した後、彼らは理解しました。</p>\n<p>「このユニットで何年も働いた後、年金を掛けたいと思っていますか？誰もこれがあなたに質問する必要のない質問だと思っています誰が年金を望んでいないのですか？この質問をデザインするのはスタンフォード大学の政治学部の教授です。経済発展の過程で政治的意味があり、徐々にそのような圧力が徐々に高まり、政治的圧力の一種となり、近代的な農業者の意識も強まっているのですが、中国独自の年金制度もあります社会化の方向に変えて、ここからどのように政治的意義を推測することができますか？</p>\n<p>もう一つの質問は、「1日にテレビを見ている時間は何時間ですか？」しかし、田舎の頻繁な停電を考えれば、結論は偏っていて、客観的な構造になりますが、主観的な選択になります。それは正確ではありません。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0072.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0072.md","frontmatter":{"title":"4月9日 土曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0072.md absPath of file >>> MarkdownRemark"}}

